import { ReactNode, useCallback, useEffect, useRef } from "react";
import AddIcon from "@digitalservicebund/icons/Add";
import PersonOutlineIcon from "@digitalservicebund/icons/PersonOutline";
import RemoveIcon from "@digitalservicebund/icons/Remove";
import RestartAltIcon from "@digitalservicebund/icons/RestartAlt";
import FileDownloadOutlinedIcon from "@digitalservicebund/icons/FileDownloadOutlined";
import { Lebensmonat } from "./Lebensmonat";
import { Elternteil, PlanFuerLebensmonat } from "./types";
import {
  finanzenProLebensmonatSelector,
  geburtstagSelector,
  planProLebensmonatSelector,
} from "./selectors";
import { useShortenListenToggle } from "./useShortenListToggle";
import { useSummarizeData } from "@/components/organisms/monatsplaner/useSummarizeData";
import { SummationFooter } from "@/components/organisms/monatsplaner/SummationFooter";
import { usePlanningContingentMonths } from "@/components/organisms/monatsplaner/usePlanningContingentMonths";
import { PlanningContingent } from "@/components/molecules";
import { useAppDispatch, useAppSelector } from "@/redux/hooks";
import { monatsplanerActions } from "@/redux/monatsplanerSlice";
import { ElterngeldType } from "@/monatsplaner";
import {
  BruttoEinkommenZeitraum,
  stepRechnerActions,
} from "@/redux/stepRechnerSlice";
import { numberOfLebensmonate } from "@/monatsplaner/configuration";
import { stepAllgemeineAngabenSelectors } from "@/redux/stepAllgemeineAngabenSlice";

export function NewMonatsplaner(): ReactNode {
  const planningContingentMonths = usePlanningContingentMonths();
  const namenDerEltern = useAppSelector(
    stepAllgemeineAngabenSelectors.getElternteilNames,
  );
  const geburtsdatum = useAppSelector(geburtstagSelector);
  const planProLebensmonat = useAppSelector(planProLebensmonatSelector);

  const dispatch = useAppDispatch();
  const changeVariant = useCallback(
    function (
      this: { elternteil: Elternteil; monthIndex: number },
      variante?: ElterngeldType,
    ): void {
      const { elternteil, monthIndex } = this;
      const oldVariante =
        planProLebensmonat[this.monthIndex][elternteil].variante;
      const shouldChange = variante !== undefined && oldVariante !== variante;

      if (shouldChange) {
        const action = monatsplanerActions.changeMonth({
          elternteil,
          monthIndex,
          targetType: variante,
          partnerMonate: variante === "PSB",
        });
        dispatch(action);
      }
    },
    [dispatch, planProLebensmonat],
  );

  const changeBruttoEinkommen = useCallback(
    function (
      this: { elternteil: Elternteil; monthIndex: number },
      newBruttoEinkommen?: number,
    ): void {
      const { elternteil, monthIndex } = this;
      const bruttoEinkommenZeitraum: BruttoEinkommenZeitraum[] =
        planProLebensmonat.map((plan, index) => {
          const oldBruttoEinkommen = plan[elternteil].bruttoEinkommen;
          const bruttoEinkommen =
            index === monthIndex ? newBruttoEinkommen : oldBruttoEinkommen;
          const from = `${index + 1}`;
          const to = from;
          return {
            bruttoEinkommen: bruttoEinkommen ?? null,
            zeitraum: { from, to },
          };
        });

      dispatch(
        stepRechnerActions.calculateBEG({
          elternteil,
          bruttoEinkommenZeitraum,
        }),
      );
    },
    [dispatch, planProLebensmonat],
  );

  const finanzenProLebensmonat = useAppSelector(finanzenProLebensmonatSelector);

  const { lastVisibleIndex, areListElementsHidden, toggleListShortening } =
    useShortenListenToggle(13, true);

  const summationData = useSummarizeData();

  const elementToViewOnRepeatPlanning = useRef<HTMLDivElement>(null);

  const repeatPlanning = useCallback(() => {
    dispatch(monatsplanerActions.resetMonths());
    elementToViewOnRepeatPlanning.current?.scrollIntoView({
      behavior: "smooth",
    });
    elementToViewOnRepeatPlanning.current?.focus({ preventScroll: true });
  }, [dispatch]);

  useEffect(() => {
    dispatch(
      stepRechnerActions.calculateBEG({
        elternteil: "ET1",
        bruttoEinkommenZeitraum: [],
      }),
    );
    dispatch(
      stepRechnerActions.calculateBEG({
        elternteil: "ET2",
        bruttoEinkommenZeitraum: [],
      }),
    );
  }, [dispatch]);

  return (
    <div>
      <div
        ref={elementToViewOnRepeatPlanning}
        className="flex max-w-[80ch] flex-col gap-16 bg-off-white"
      >
        <PlanningContingent
          className="p-16 print:hidden"
          months={planningContingentMonths}
        />

        <div className="flex flex-col border-2 border-off-white bg-white py-16">
          <div className="flex justify-evenly font-bold">
            <span>
              <PersonOutlineIcon /> {namenDerEltern.ET1}
            </span>

            <span>
              <PersonOutlineIcon /> {namenDerEltern.ET2}
            </span>
          </div>

          <span className="mb-6 text-center text-14">Lebensmonate</span>

          {Array.from({ length: numberOfLebensmonate }, (_, monthIndex) => {
            const lebensmonat = monthIndex + 1;
            const plan = planProLebensmonat[monthIndex];
            const finanzen = finanzenProLebensmonat[monthIndex];
            const onPlanChanges = (newPlan: PlanFuerLebensmonat) => {
              const { ET1, ET2 } = newPlan;
              changeVariant.call(
                { elternteil: "ET1", monthIndex },
                ET1.variante,
              );
              changeVariant.call(
                { elternteil: "ET2", monthIndex },
                ET2.variante,
              );
              changeBruttoEinkommen.call(
                { elternteil: "ET1", monthIndex },
                ET1.bruttoEinkommen,
              );
              changeBruttoEinkommen.call(
                { elternteil: "ET2", monthIndex },
                ET2.bruttoEinkommen,
              );
            };
            const isHidden = monthIndex > lastVisibleIndex;

            return (
              <Lebensmonat
                key={monthIndex}
                className="w-full"
                geburtsdatum={geburtsdatum}
                lebensmonat={lebensmonat}
                plan={plan}
                onPlanChanges={onPlanChanges}
                finanzen={finanzen}
                isHidden={isHidden}
              />
            );
          })}

          <hr className="mx-24 my-10 border border-off-white" />

          <button
            type="button"
            onClick={toggleListShortening}
            className="font-bold text-primary print:hidden"
          >
            {areListElementsHidden ? <AddIcon /> : <RemoveIcon />}
            {areListElementsHidden ? "mehr" : "weniger"} Monate zeigen
          </button>
        </div>

        <SummationFooter className="w-full p-16" data={summationData} />
      </div>

      <div className="my-32 flex gap-32 text-primary print:hidden">
        <button type="button" onClick={repeatPlanning}>
          <RestartAltIcon />{" "}
          <span className="underline">Planung wiederholen</span>
        </button>

        <button type="button" onClick={window.print}>
          <FileDownloadOutlinedIcon />{" "}
          <span className="underline">Download der Planung</span>
        </button>
      </div>
    </div>
  );
}
