import { ReactNode } from "react";
import AddIcon from "@digitalservicebund/icons/Add";
import classNames from "classnames";
import { ElterngeldType } from "@/monatsplaner";

type Props = {
  readonly variante?: ElterngeldType;
  readonly className?: string;
};

export function ElterngeldvarianteBadge({
  variante,
  className,
}: Props): ReactNode {
  const label = variante ? LABELS[variante] : <AddIcon />;
  const variantClassName = variante
    ? CLASS_NAME[variante]
    : UNDEFINDED_CLASS_NAME;

  return (
    <span
      className={classNames(
        "py-8 px-4 rounded text-center font-bold flex items-center justify-center leading-4 min-h-56",
        className,
        variantClassName,
      )}
    >
      {label}
    </span>
  );
}

const LABELS: Record<ElterngeldType, string> = {
  BEG: "Basis",
  "EG+": "Plus",
  PSB: "Bonus",
  None: "kein Elterngeld",
};

const CLASS_NAME: Record<ElterngeldType, string> = {
  BEG: "elterngeld-basis",
  "EG+": "elterngeld-plus",
  PSB: "elterngeld-bonus",
  None: "elterngeld-none",
};

const UNDEFINDED_CLASS_NAME = "bg-off-white border-grey border-2 text-black";
