import { ReactNode } from "react";
import ToggleOnIcon from "@digitalservicebund/icons/ToggleOn";
import ToggleOffIcon from "@digitalservicebund/icons/ToggleOff";

type Props = {
  readonly label: string;
  readonly isChecked: boolean;
  readonly onToggle: (isChecked: boolean) => void;
  readonly className?: string;
};

export function ToggleButton({
  label,
  isChecked,
  onToggle,
  className,
}: Props): ReactNode {
  const Icon = isChecked ? ToggleOnIcon : ToggleOffIcon;

  const toggleIsChecked = () => onToggle(!isChecked);

  return (
    <button
      type="button"
      role="switch"
      aria-checked={isChecked}
      onClick={toggleIsChecked}
      className={className}
    >
      <Icon /> {label}
    </button>
  );
}
