import { ReactNode, memo } from "react";
import classNames from "classnames";
import { ElterngeldProVariante } from "./types";
import { ElterngeldType } from "@/monatsplaner";
import { formatAsCurrency } from "@/utils/locale-formatting";

type Props = {
  readonly name: string;
  readonly checkedVariante?: ElterngeldType;
  readonly onChange: (value: ElterngeldType) => void;
  readonly elterngeldProVariante: ElterngeldProVariante;
};

export const ElterngeldvariantenRadioButtonGroup = memo(
  function ElterngeldvariantenRadioButtonGroup({
    name,
    checkedVariante,
    onChange,
    elterngeldProVariante,
  }: Props): ReactNode {
    return (
      <>
        {OPTIONS.map(({ variante, label, className }) => {
          const isChecked = variante === checkedVariante;
          const checkThisOption = () => onChange(variante);
          const elterngeld = elterngeldProVariante[variante];
          const formattedElterngeld = formatAsCurrency(elterngeld);

          // TODO: How to use `checked:` styling on label?
          return (
            <label
              key={variante}
              className={classNames(
                "font-bold p-8 rounded leading-4 gap-4 min-h-56 flex justify-center items-center",
                "focus-within:outline-2 hover:underline ",
                { "ring-4": isChecked },
                className,
              )}
            >
              <input
                type="radio"
                className="!focus:outline-none appearance-none"
                name={name}
                value={variante}
                checked={isChecked}
                onChange={checkThisOption}
              />
              {label}
              {elterngeld > 0 && (
                <span className="font-regular">{formattedElterngeld}</span>
              )}
            </label>
          );
        })}
      </>
    );
  },
);

const OPTIONS: Option[] = [
  {
    variante: "BEG",
    label: "Basis",
    className: "elterngeld-basis",
  },
  {
    variante: "EG+",
    label: "Plus",
    className: "elterngeld-plus",
  },
  {
    variante: "PSB",
    label: "Bonus",
    className: "elterngeld-bonus",
  },
  {
    variante: "None",
    label: "kein Elterngeld",
    className: "elterngeld-none",
  },
];

type Option = {
  variante: ElterngeldType;
  label: string;
  className: string;
};
