import { ReactNode } from "react";
import BusinessCenterOutlinedIcon from "@digitalservicebund/icons/BusinessCenterOutlined";
import classNames from "classnames";
import { FinanzenFuerElternteil } from "./types";
import { formatAsCurrency } from "@/utils/locale-formatting";
import { ElterngeldType } from "@/monatsplaner";

type Props = {
  readonly finanzen: FinanzenFuerElternteil;
  readonly variante?: ElterngeldType;
  readonly className?: string;
};

export function Finanzen({ finanzen, variante, className }: Props): ReactNode {
  const { elterngeldProVariante, nettoEinkommen } = finanzen;
  const elterngeld = variante ? elterngeldProVariante[variante] : 0;
  const formattedElterngeld = formatAsCurrency(elterngeld);
  const formattedNettoEinkommen =
    !!nettoEinkommen && formatAsCurrency(nettoEinkommen);

  return (
    <div
      className={classNames(
        "flex flex-col items-center justify-center",
        className,
      )}
    >
      {!!elterngeld && <span className="font-bold">{formattedElterngeld}</span>}

      {!!nettoEinkommen && (
        <span>
          <BusinessCenterOutlinedIcon /> {formattedNettoEinkommen}
        </span>
      )}
    </div>
  );
}
