import { ChangeEvent, ReactNode, useCallback, useMemo, useState } from "react";
import ExpandLessIcon from "@digitalservicebund/icons/ExpandLess";
import ExpandMoreIcon from "@digitalservicebund/icons/ExpandMore";
import classNames from "classnames";
import { ElterngeldvarianteBadge } from "./ElterngeldvarianteBadge";
import { ToggleButton } from "./ToggleButton";
import { ElterngeldvariantenRadioButtonGroup } from "./ElterngeldvariantenRadioButtonGroup";
import { Finanzen } from "./Finanzen";
import { ZeitraumHeading } from "./ZeitraumHeading";
import {
  Elternteil,
  FinanzenFuerLebensmonat,
  PlanFuerLebensmonat,
} from "./types";
import { ElterngeldType } from "@/monatsplaner";
import { closeOtherOpenElementsOfSameGroup } from "@/utils/dialogs";

type Props = {
  readonly geburtsdatum: Date;
  readonly lebensmonat: number;
  readonly plan: PlanFuerLebensmonat;
  readonly onPlanChanges: (plan: PlanFuerLebensmonat) => void;
  readonly finanzen: FinanzenFuerLebensmonat;
  readonly className?: string;
  readonly isHidden?: boolean;
};

export function Lebensmonat({
  geburtsdatum,
  lebensmonat,
  plan,
  onPlanChanges,
  finanzen,
  className,
  isHidden,
}: Props): ReactNode {
  const chooseVariant = useCallback(
    function (this: Elternteil, variant: ElterngeldType): void {
      onPlanChanges({
        ...plan,
        [this]: { ...plan[this], variante: variant },
      });
    },
    [plan, onPlanChanges],
  );

  const [chooseVariantForET1, chooseVariantForET2] = useMemo(
    () => [chooseVariant.bind("ET1"), chooseVariant.bind("ET2")],
    [chooseVariant],
  );

  const setBruttoEinkommen = useCallback(
    function (this: Elternteil, event: ChangeEvent<HTMLInputElement>): void {
      const value = event.target.value.trim();
      const isEmpty = value.length === 0;
      const bruttoEinkommen = isEmpty ? undefined : Number.parseInt(value);
      const canBeSet = bruttoEinkommen === undefined || !isNaN(bruttoEinkommen);

      if (canBeSet) {
        const newPlan = {
          ...plan,
          [this]: { ...plan[this], bruttoEinkommen },
        };
        onPlanChanges(newPlan);
      }
    },
    [plan, onPlanChanges],
  );

  const [setBruttoEinkommenForET1, setBruttoEinkommenForET2] = useMemo(
    () => [setBruttoEinkommen.bind("ET1"), setBruttoEinkommen.bind("ET2")],
    [setBruttoEinkommen],
  );

  const resetEinkommen = useCallback(() => {
    const ET1 = { ...plan.ET1, einkommenBrutto: undefined };
    const ET2 = { ...plan.ET2, einkommenBrutto: undefined };
    onPlanChanges({ ...plan, ET1, ET2 });
  }, [plan, onPlanChanges]);

  const anyParentHasBonusMonth = [
    plan.ET1.variante,
    plan.ET2.variante,
  ].includes("PSB");

  const [isEinkommenToggleChecked, setIsEinkommenToggleChecked] = useState(
    !!plan.ET1.bruttoEinkommen || !!plan.ET2.bruttoEinkommen,
  );

  const isEinkommenSectionVisible =
    isEinkommenToggleChecked || anyParentHasBonusMonth;

  const toggleEinkommenSection = useCallback(
    (isChecked: boolean) => {
      if (anyParentHasBonusMonth) return;
      setIsEinkommenToggleChecked(isChecked);
      !isChecked && resetEinkommen();
    },
    [resetEinkommen, anyParentHasBonusMonth],
  );

  const expandIconClassName = "absolute right-4 bottom-1/2 translate-y-1/2";
  const gridColumnTemplateClassName = "grid grid-cols-[6fr,1fr,6fr]";

  return (
    <details
      className={classNames(
        "open:bg-off-white hover:bg-off-white group",
        className,
      )}
      name="Lebensmonat"
      onToggle={closeOtherOpenElementsOfSameGroup}
      hidden={isHidden}
    >
      <summary
        className={classNames(
          "relative list-none px-32 my-10 gap-4 justify-center hover:cursor-pointer",
          gridColumnTemplateClassName,
        )}
      >
        <div className="flex flex-wrap-reverse items-center">
          <Finanzen
            finanzen={finanzen.ET1}
            variante={plan.ET1.variante}
            className="grow"
          />

          <ElterngeldvarianteBadge
            variante={plan.ET1.variante}
            className="shrink basis-112"
          />
        </div>

        <span className="flex items-center justify-center px-2 font-bold">
          {lebensmonat}
        </span>

        <div className="flex flex-wrap items-center">
          <ElterngeldvarianteBadge
            variante={plan.ET2.variante}
            className="shrink basis-112"
          />

          <Finanzen
            finanzen={finanzen.ET2}
            variante={plan.ET2.variante}
            className="grow"
          />
        </div>

        <ExpandMoreIcon
          className={classNames(expandIconClassName, "group-open:hidden")}
        />
        <ExpandLessIcon
          className={classNames(
            expandIconClassName,
            "hidden group-open:inline",
          )}
        />
      </summary>

      <div
        className={classNames(
          "mx-20 mb-24 mt-20 gap-y-10",
          gridColumnTemplateClassName,
        )}
      >
        <ZeitraumHeading
          geburtsdatum={geburtsdatum}
          lebensmonat={lebensmonat}
          className="col-start-1 col-end-[-1] text-center"
        />

        <fieldset
          aria-label={`Elterngeldvariante Lebensmonat ${lebensmonat} ET1`}
          className="col-start-1 flex justify-end"
        >
          <div className="flex max-w-[20ch] grow flex-col gap-10">
            <ElterngeldvariantenRadioButtonGroup
              name={`lebensmonat-${lebensmonat}-variante-ET1`}
              checkedVariante={plan.ET1.variante}
              onChange={chooseVariantForET1}
              elterngeldProVariante={finanzen.ET1.elterngeldProVariante}
            />
          </div>
        </fieldset>

        <fieldset
          aria-label={`Elterngeldvariante Lebensmonat ${lebensmonat} ET2`}
          className="col-start-3 flex"
        >
          <div className="flex max-w-[20ch] grow flex-col gap-10">
            <ElterngeldvariantenRadioButtonGroup
              name={`lebensmonat-${lebensmonat}-variante-ET2`}
              checkedVariante={plan.ET2.variante}
              onChange={chooseVariantForET2}
              elterngeldProVariante={finanzen.ET2.elterngeldProVariante}
            />
          </div>
        </fieldset>

        <ToggleButton
          className="col-start-1 col-end-[-1] mt-16"
          isChecked={isEinkommenToggleChecked || anyParentHasBonusMonth}
          onToggle={toggleEinkommenSection}
          label="Bruttoeinkommen in diesem Monat"
        />

        {!!isEinkommenSectionVisible && (
          <>
            <div className="cols-start-1 flex justify-end">
              <label className="flex max-w-[20ch] grow flex-col gap-10">
                Einkommen in €
                <input
                  inputMode="numeric"
                  className="w-full appearance-none px-16 py-8 focus:ring-2 focus:ring-primary"
                  value={plan.ET1.bruttoEinkommen ?? ""}
                  onChange={setBruttoEinkommenForET1}
                  placeholder="0"
                />
              </label>
            </div>

            <div className="col-start-3 flex">
              <label className="flex max-w-[20ch] grow flex-col gap-10">
                Einkommen in €
                <input
                  inputMode="numeric"
                  className="w-full appearance-none px-16 py-8 focus:ring-2 focus:ring-primary"
                  value={plan.ET2.bruttoEinkommen ?? ""}
                  onChange={setBruttoEinkommenForET2}
                  placeholder="0"
                />
              </label>
            </div>
          </>
        )}
      </div>
    </details>
  );
}
