import { SyntheticEvent } from "react";

export function closeOtherOpenElementsOfSameGroup(
  event: SyntheticEvent<HTMLDetailsElement, ToggleEvent>,
): void {
  const targetElement = event.target as Element;
  const isDetailsElement = targetElement.tagName.toLowerCase() === "details";
  const nameAttributeValue = targetElement.getAttribute("name");
  const hasNameAttribute = nameAttributeValue !== null;
  const isPartOfDetailsGroup = isDetailsElement && hasNameAttribute;
  const hasOpened = targetElement.getAttribute("open") !== null;

  if (isPartOfDetailsGroup && hasOpened) {
    const openElementsOfGroup = Array.from(
      document.querySelectorAll(`details[name=${nameAttributeValue}][open]`),
    );

    const elementsToClose = openElementsOfGroup.filter(
      (element) => element !== targetElement,
    );

    elementsToClose.forEach((element) => element.removeAttribute("open"));
  }
}
