import {
  FinanzenFuerElternteil,
  FinanzenFuerLebensmonat,
  PlanFuerLebensmonat,
} from "./types";
import { createAppSelector } from "@/redux/hooks";
import { numberOfLebensmonate } from "@/monatsplaner/configuration";
import {
  BruttoEinkommenZeitraum,
  ElterngeldRow,
  ElterngeldRowsResult,
} from "@/redux/stepRechnerSlice";

export const geburtstagSelector = createAppSelector(
  [(state) => state.stepNachwuchs.wahrscheinlichesGeburtsDatum],
  (rawDate) => parseGermanDateText(rawDate),
);

function parseGermanDateText(rawDate: string): Date {
  const [day, month, year] = rawDate.split(".");
  const isoDateString = `${month}-${day}-${year}`;
  const parsedDate = Date.parse(isoDateString);
  return new Date(parsedDate);
}

export const finanzenProLebensmonatSelector = createAppSelector(
  [
    (state) => state.stepRechner.ET1.elterngeldResult,
    (state) => state.stepRechner.ET2.elterngeldResult,
  ],
  (elterngeldResultET1, elterngeldResultET2): FinanzenFuerLebensmonat[] =>
    Array.from({ length: numberOfLebensmonate }, (_, monthIndex) => ({
      ET1: elterngeldResultToFinanzenFuerElternteil(
        elterngeldResultET1,
        monthIndex,
      ),
      ET2: elterngeldResultToFinanzenFuerElternteil(
        elterngeldResultET2,
        monthIndex,
      ),
    })),
);

export const planProLebensmonatSelector = createAppSelector(
  [
    (state) => state.monatsplaner.elternteile.ET1.months,
    (state) => state.monatsplaner.elternteile.ET2.months,
    (state) => state.stepRechner.ET1.bruttoEinkommenZeitraum,
    (state) => state.stepRechner.ET2.bruttoEinkommenZeitraum,
  ],
  (monthsET1, monthsET2, einkommenET1, einkommenET2): PlanFuerLebensmonat[] =>
    Array.from({ length: numberOfLebensmonate }, (_, monthIndex) => {
      const varianteET1 = monthsET1[monthIndex].type;
      const varianteET2 = monthsET2[monthIndex].type;
      const bruttoEinkommenET1 = findBruttoEinkommen(einkommenET1, monthIndex);
      const bruttoEinkommenET2 = findBruttoEinkommen(einkommenET2, monthIndex);

      return {
        ET1: { variante: varianteET1, bruttoEinkommen: bruttoEinkommenET1 },
        ET2: { variante: varianteET2, bruttoEinkommen: bruttoEinkommenET2 },
      };
    }),
);

function elterngeldResultToFinanzenFuerElternteil(
  elterngeldResult: ElterngeldRowsResult,
  monthIndex: number,
): FinanzenFuerElternteil {
  const rowForMonth = findElterngeldRow(elterngeldResult, monthIndex);
  const nettoEinkommen = rowForMonth?.nettoEinkommen;
  const elterngeldProVariante = {
    BEG: rowForMonth?.basisElternGeld ?? 0,
    "EG+": rowForMonth?.elternGeldPlus ?? 0,
    PSB: rowForMonth?.elternGeldPlus ?? 0,
    None: 0,
  };
  return { nettoEinkommen, elterngeldProVariante };
}

function findElterngeldRow(
  elterngeldResult: ElterngeldRowsResult,
  monthIndex: number,
): ElterngeldRow | undefined {
  const lebensmonat = monthIndex + 1;
  const rows =
    elterngeldResult.state === "success" ? elterngeldResult.data : [];

  return rows.find(
    ({ vonLebensMonat, bisLebensMonat }) =>
      vonLebensMonat <= lebensmonat && lebensmonat <= bisLebensMonat,
  );
}

function findBruttoEinkommen(
  einkommen: BruttoEinkommenZeitraum[],
  monthIndex: number,
): number | undefined {
  const lebensmonat = monthIndex + 1;
  const einkommenForMonth = einkommen.find(
    ({ zeitraum }) =>
      Number.parseInt(zeitraum.from) <= lebensmonat &&
      lebensmonat <= Number.parseInt(zeitraum.to),
  );
  return einkommenForMonth?.bruttoEinkommen ?? undefined;
}
