import { useCallback, useState } from "react";

export function useShortenListenToggle(
  lastIndexOnShorten: number,
  initiallyShorten: boolean,
) {
  const [lastVisibleIndex, setLastVisibleIndex] = useState(lastIndexOnShorten);
  const [areListElementsHidden, setAreListElementsHidden] =
    useState(initiallyShorten);

  const toggleListShortening = useCallback(() => {
    setAreListElementsHidden(!areListElementsHidden);
    const actualAreListElementsHidden = !areListElementsHidden;
    setLastVisibleIndex(
      actualAreListElementsHidden
        ? lastIndexOnShorten
        : Number.MAX_SAFE_INTEGER,
    );
  }, [areListElementsHidden, lastIndexOnShorten]);

  return { lastVisibleIndex, areListElementsHidden, toggleListShortening };
}
