import { ReactNode } from "react";

type Props = {
  readonly geburtsdatum: Date;
  readonly lebensmonat: number;
  readonly className?: string;
};

export function ZeitraumHeading({
  geburtsdatum,
  lebensmonat,
  className,
}: Props): ReactNode {
  const timeSpan = calculateTimeSpan(geburtsdatum, lebensmonat);
  const formattedTimeSpan = formatTimeSpan(timeSpan);
  return <span className={className}>Zeitraum: {formattedTimeSpan}</span>;
}

function calculateTimeSpan(geburtsdatum: Date, lebensmonat: number): TimeSpan {
  const from = new Date(geburtsdatum);
  from.setMonth(geburtsdatum.getMonth() + lebensmonat - 1);

  const to = new Date(geburtsdatum);
  to.setMonth(geburtsdatum.getMonth() + lebensmonat);
  to.setDate(to.getDate() - 1);

  return { from, to };
}

function formatTimeSpan(timeSpan: TimeSpan): string {
  const from = timeSpan.from.toLocaleDateString();
  const to = timeSpan.to.toLocaleDateString();
  return `${from} bis ${to}`;
}

type TimeSpan = {
  from: Date;
  to: Date;
};
